import React, { useEffect, useState } from "react";
import GoogleMapReact from "google-map-react";
import { MapContainer } from "../styles/styles";
import RaptorMarker from "../markers/RaptorMarker";
import whiteMap from "../styles/whiteMap.json";
import Smiler from "../graphics/Smiler";

const CMap = ({ history, mapKey, places }) => {
  const [userLocation, setUserLocation] = useState();
  const [markers, setMarkers] = useState([]);

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(p => {
      const { latitude: lat, longitude: lng } = p.coords;
      setUserLocation({ lat, lng });
    });
  }, []);

  if (!userLocation) {
    return (
      <div>
        <Smiler id="smiler" style={{ fill: "red" }} />
      </div>
    );
  }
  console.log(markers);
  return (
    <MapContainer>
      <GoogleMapReact
        // defaultCenter={places.ppark}
        defaultCenter={userLocation}
        defaultZoom={17}
        bootstrapURLKeys={{ key: mapKey }}
        options={{ styles: whiteMap }}
        onClick={e => {
          const marker = {
            key: "frog" + markers.length,
            id: "frog" + markers.length,
            found: false,
            lat: e.lat,
            lng: e.lng
          };
          setMarkers([...markers, marker]);
        }}
        // onChildClick={p => {
        //   history.push(`/pop/${p}`);
        // }}
      >
        {markers.map(m => {
          return (
            <RaptorMarker
              key={m.hash}
              id={m.hash}
              found={m.found}
              lat={m.lat}
              lng={m.lng}
            />
          );
        })}
      </GoogleMapReact>
    </MapContainer>
  );
};

export default CMap;
